    import {
    		mapState,
    		mapMutations
    	} from 'vuex';
	import apiName from '../../static/commonjs/request/apiName.js'
	export default {
		data() {
			return {
				info:{},
				is_audition_type:''
			}
		},
		computed: {
			...mapState(['token','presentAddress','jobCategory','indexSearchJob'])
		},
		methods: {
			goBack(){
				uni.navigateBack()
			},
			onLoad(options){
				this.getInfo(options.id)
			},
			navigateMap(){
				let _this = this;
				uni.openLocation({
					latitude: parseFloat(_this.info.map_y),
					longitude: parseFloat(_this.info.map_x),
					fail(error) {
						console.log(error)
					}
				});
			},
			telPhone(num){
				uni.makePhoneCall({
				    phoneNumber:num
				});
			},
			getInfo(id){
                this.$http.post(apiName.GET_INVITE_DETAIL,{
                	params:{
                		token:this.token,
                		invite_id:id
                	}
                }).then(res => {
					console.log(res);
					this.info = res.data.data;
					if(res.data.data.audition_type == 1){
						this.is_audition_type = '现场面试'
					}else{
						this.is_audition_type = '视频面试'
					}
				}).catch(err => {
					console.log(err);
				})
			}
		}
	}
